body {
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #333333;
  background-color: #f2f2f2; }

@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  width: 100%;
  min-height: 800px;
  background-repeat: no-repeat;
  background-image: url("../img/photo.png"); }

.main-nav {
  display: flex;
  padding-top: 64px;
  padding-bottom: 63px;
  justify-content: space-between;
  background-color: #3e4850;
  opacity: 0.85;
  text-transform: uppercase; }

.main-nav__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0; }

.main-nav__item {
  padding-left: 40px; }

.main-nav__link {
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
  color: #ffffff;
  text-decoration: none; }

.main-nav__wrapper {
  display: flex;
  width: 1170px;
  margin: 0 auto;
  justify-content: space-between; }

.main-nav__link:hover {
  color: #4bcaff;
  border: 2px solid #4bcaff; }

.main-nav__link:active {
  color: #4bcaff;
  border: 2px solid #4bcaff;
  opacity: .3; }

.main-nav__logo:active {
  opacity: .3; }

.container {
  width: 1170px;
  margin: 0 auto; }

.wrapper {
  width: 1920px;
  margin: 0 auto; }

.welcome__text {
  font-size: 35px;
  line-height: 75px;
  font-weight: 300;
  text-transform: uppercase;
  margin: 0;
  padding-top: 260px;
  padding-bottom: 235px;
  padding-left: 107px;
  padding-right: 112px;
  color: #ffffff;
  text-align: center; }

.welcome__background {
  background-color: #3e4850;
  padding: 18px;
  opacity: 0.85; }

.page-main {
  background-color: #4bcaff; }

.services {
  padding-top: 80px;
  padding-bottom: 75px;
  color: #ffffff; }

.services__head {
  font-size: 30px;
  line-height: 24px;
  font-weight: 300;
  text-transform: uppercase;
  margin: 0; }

.services__title {
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
  text-transform: uppercase; }

.services__item {
  width: 300px;
  padding-top: 50px;
  list-style: none; }

.services__description {
  font-size: 12px;
  line-height: 18px;
  font-weight: 700; }

.services__list {
  display: flex;
  padding: 0;
  justify-content: space-around; }

.services__item--coffee,
.services__item--instant,
.services__item--serious,
.services__item--frame {
  background-repeat: no-repeat; }

.services__item--coffee {
  background-image: url("../img/coffee.png"); }

.services__item--instant {
  background-image: url("../img/instant.png"); }

.services__item--serious {
  background-image: url("../img/serious.png"); }

.services__item--frame {
  background-image: url("../img/frame.png"); }

.portfolio {
  padding-top: 80px;
  padding-bottom: 50px; }

.portfolio__head {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  padding-bottom: 90px; }

.portfolio__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0; }

.portfolio__link {
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  text-decoration: none;
  color: #515f67;
  padding: 10px 20px; }

.portfolio__item {
  padding-left: 40px; }

.portfolio__link:hover {
  color: #4bcaff;
  border: 2px solid #4bcaff; }

.portfolio__link:active {
  color: #4bcaff;
  border: 2px solid #4bcaff;
  opacity: .3; }

.portfolio__tile {
  font-size: 30px;
  line-height: 24px;
  font-weight: 300;
  margin: 0;
  color: #515f67; }

.portfolio__images {
  display: inline-block;
  padding-bottom: 30px; }

.portfolio__img {
  background-color: #979797;
  opacity: 0.8; }

.portfolio__photos {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.portfolio__img:hover {
  background-color: #3e4850;
  opacity: 1; }

.button {
  position: absolute;
  top: 20%;
  right: 36%;
  display: block;
  width: 32px;
  height: 33px;
  cursor: pointer;
  border: none;
  background-color: inherit; }

.button::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 0;
  width: 33px;
  height: 4px;
  background-color: #ffffff;
  transform: rotate(90deg); }

.button::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 0;
  width: 32px;
  height: 4px;
  background-color: #ffffff; }
